.cssani_groud{
  width: 100%;height: 1000px;
  //background: #afafaf;
  position: relative;

  .nor_total{
    position: relative;left: 20%;
    top: 50%;transform: translateY(-50%);
    width: 308px;height: 390px;
  
    .nor_head{
      position: absolute;z-index: 1;
      left:calc(50% - 121px);top: 0%;
      width: 242px;height:213px;
      animation: headrota 4s ease-in-out 0s infinite;
      .ear{
        position: absolute;z-index: 2;
        left:-8px;top:2px;
        transform: rotate(-26deg);
        width: 98px;height: 60px;
        background: #fff;box-sizing: border-box;
        border-radius: 50px 50px 0 0;
        border: 6px solid #4e4741;border-bottom:none;
        &::after{
        content:"";position: absolute;left:140px;top:67px;
        transform: rotate(54deg);
        width: 98px;height: 60px;background: #fff;
        border-radius: 50px 50px 0 0;border: 6px solid #4e4741;
        border-bottom: none;
        box-sizing: border-box;
        }
      }
  
      .face{
  
        position: absolute;left:0;top: 34px;
        width: 242px;height: 180px;background: #fff;border: 6px solid #4e4741;
        box-sizing: border-box;border-radius: 80px 80px 95px 95px;
        overflow: hidden;
  
        .eyes{
        position: absolute;left: calc(50% - 4px);top: 45px;
        width: 8px;height: 8px;/*background: #4e4741;*/
        border-radius: 50%;
        box-shadow: 40px 0 0 0 #4e4741,-40px 0 0 0 #4e4741;
        }
        .mouth{
          position: absolute;left: calc(50% - 20px);top: calc(50% - 10px);
          width: 40px;height: 20px;background: #ffa68e;transform: translateY(5px) rotate(-90deg);
          box-sizing: border-box;border-radius:50% 10% 10% 50%/50% 10% 10% 50%;
          box-shadow: -1px 0 0 3.5px #4e4741;
        }
        .lip{
          position:absolute;left: calc(50% - 3px);top: 60px;
          width:6px;height: 6px;border-radius: 50%;background:#4e4741;
          box-shadow: -33px 1.6px 0 0 #4e4741,33px 1.6px 0 0 #4e4741;
  
          &::after{content: "";position: absolute;
        width:50px;height:40px;border-radius: 50%;
        border: 6px solid #4e4741;box-sizing: border-box;
        border-top-color: transparent;
        border-left-color: transparent;
        border-right-color: transparent;
        left:-5px;top:-27px;
      }
          &::before{content: "";position: absolute;
        width:50px;height:40px;border-radius: 50%;
        border: 6px solid #4e4741;box-sizing: border-box;
        border-top-color: transparent;
        border-left-color: transparent;
        border-right-color: transparent;
        left:-39px;top: -27px;}
        }
        .cheek{
          position: absolute;left: calc(50% - 43px);top: calc(50% - 43px);
            width: 86px;height: 86px;/*background: #ffa68e;*/
          border-radius: 50%;
           box-shadow: 86px 0 1px 0 #ffd8ce,-86px 0 1px #ffd8ce;
        
      }
    }
  }
  
    @keyframes headrota {
      0%{transform: rotate(0deg);}
      25%{transform: rotate(10deg);}
      50%{transform: rotate(0deg);}
      75%{transform: rotate(-10deg);}
      100%{transform: rotate(0deg);}
     }
  
    .nor_body{
  
      position:absolute;z-index:0;
      left: calc(50% - 154xp);bottom: 0%;
      width: 308px;height: 212px;
  
      .arm_left{
        position: absolute;left: 120px;top:-30px;
        width: 48px;height: 140px;background: #fff;
        border: 6px solid #4e4741;
        border-radius: 25px;
        transform: rotate(55deg);transform-origin:0% 0%;
        animation: move_arm 1.5s ease-in 0s infinite;
      }
      @keyframes move_arm {
        25%{transform: rotate(140deg);}
        45%{transform: rotate(100deg);}
     
        75%{transform: rotate(140deg);}
        90%{transform: rotate(100deg);}
       }
      .arm_right{
        position: absolute;left: 130px;top: -30px;
        width: 48px;height: 140px;background: #fff;
        border: 6px solid #4e4741;
        border-radius: 25px;transform-origin: 100% 0;
        transform: rotate(-55deg);
        animation: moves_arm 1.5s ease-in 0s infinite;
      }
  
      @keyframes moves_arm {
        25%{transform: rotate(-150deg);}
        45%{transform: rotate(-100deg);}
     
        75%{transform: rotate(-150deg);}
        90%{transform: rotate(-100deg);}
       }
  
      .nor_middel{
        position: absolute;left:calc(50% - 95px);top:0px;
        width:190px ;height:160px;background:#fff;
        border: 6px solid #4e4741;box-sizing: border-box;
      }
      .leg{
        position: absolute;left: 59px;top:154px;
        width: 80px;height: 50px;background:#fff;
        border: 5px solid #4e4741;box-sizing: border-box;
        border-top: none;border-radius:0 0 15px 15px;
  
        &::after{
          content: "";position:absolute;
          width: 80px;height: 50px;background: #fff;
          border: 5px solid #4e4741;box-sizing: border-box;
          border-top: none;border-radius: 0 0 15px 15px;
          transform: translate(105px);
        }
      }
    }
    }


    .csstext{
    position: absolute;right: 20%;top:50%;transform: translateY(-50%);
     .cssboxin{
      width: 25rem;height: 24rem;
      border-radius: 600% 700% 700% 650%;
      background: rgba(255, 255, 0, 0.366);
      padding-top: 30%;
      box-sizing: border-box;
      animation: aniborder 5s linear 0s infinite;
      h4{
        text-align: center;
        font-size: 2.5rem;
      }
      p{
        text-align: center;
        font-size: 1.7rem;
      }
      div{
        margin: 0 auto;
        width: 60%;
        margin-top: 20px;
        display: flex;
        justify-content: space-evenly;
        button{
          cursor: pointer;
          background: #fff;border: 1px solid #000;
          padding: 10px;
          width: 110px;
          font-size: 1rem;
          border-radius: 30px;
          //margin: 0 auto;
        }
      }
     }

     @keyframes aniborder {
      0%{border-radius: 600% 700% 700% 650%;}
      25%{border-radius: 500% 800% 600% 750%;}
      50%{border-radius: 700% 600% 500% 650%;}
      75%{border-radius: 650% 850% 750% 900%;}
      100%{border-radius: 600% 700% 700% 650%;}
     }
    
    }
}




